/*  bio sections */

@import 'variables';
@import 'typography';

.bio-in-brief {

  @include standard-content-layout;

  @media only screen and (min-width: 800px) {
    grid-column-start: 2;
    grid-column-end: 5;
  }

  height: 80vh;
  position: relative;
  padding: 0;

  h3{
    position: absolute;
    top: 40%;
    transform: translateY(-40%);
    font: $hello-message-font;
  }

  h3.salutations{
    font-weight: 400;
  }
}
.bio-image{

  display: none;
  position: relative;
  padding: 0;

  @media only screen and (min-width: 800px) {
    display: block;
    grid-column-start: 6;
    grid-column-end: 10;
  }

  img{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    max-width:100%;
  }
}


//Homepage section specifics
section {

  .section-title{
    margin-bottom: 6*$grid-unit;

    h3 {
      display: inline-block;
      font: $section-title-font;
      padding-bottom: 2px;
      margin-bottom: $grid-unit;

      /* anchor styling */
      a{
        color: $neutral-dark;
        pointer-events: none;
      }
    }

    .description{
      margin-bottom: 0*$grid-unit;
      margin-top: 2*$grid-unit;
      font: $large-content-font;
    }
  }
}

/* my work */
.my-work{
  background: red;
  @include full-content-layout;
  @include grid-template;

  .description{
    @include full-content-layout;
    @include grid-template;

    .text{

      .title{
        display: block;
      }
    }



    &.left{
      img{

        @media only screen and (min-width: 800px) {
          display: block;
          grid-column-start: 1;
          grid-column-end: 6;

          width: 100%
        }
      }

      .text{
        display: block;
        grid-column-start: 6;
        grid-column-end: 10;
        background: blue;
      }

    }

    &.right{

    }
  }

}

/* articles section */
.articles {

  p{
    font: $large-content-font;
  }

  p.more-link{
    font-style: italic;
  }

  ul.articles-list{

    padding-left: 0;

    li.article{
      margin-bottom: $grid-unit * 6;
      display: flex;


      img.article-preview{
        display: inline-block;
        width: $grid-unit*14;
        margin-right: $grid-unit*4;
      }

      .article-summary{
        flex: 1;

          .article-title{
            font: $content-title-font;
            display: block;
            color: $neutral-dark;
          }
          .article-description{
            font: $regular-content-font;
            color: $neutral-light-2;
            display: block;
          }

      }

    }



  }
}

.case-studies {
  @include full-content-layout;
  @include grid-template;

  .section-title{
    @include standard-content-layout;
  }

  .project-tile{
    @include wide-content-layout;
    @include grid-template;

    margin-bottom: $grid-unit * 8;

    .project-image-link {
      @include wide-content-layout;

      & img{
        width: 100%;

        @media only screen and (min-width: 800px) {
          display: block;
          width: auto;
          margin: auto;
        }
      }

      &.victorops-android{
        background: $orange-light;
      }

      &.victorops-onboarding{
        background: $orange-light;
      }

      &.pivotal-tracker-analytics{
        background: $blue-light-2;
      }

      &.listacular{
        background: $blue-light-2;
      }

    }

    .project-description{

      @include standard-content-layout;

      .project-title{
        font: $content-title-font;
        margin-bottom: $grid-unit;
        margin-top: $grid-unit;
      }

      .project-summary{
        margin: 0;
        font: $light-content-font;
        color: $neutral-light-2;
      }
    }
  }
}

